import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { changeErrorText } from "../../store/features/connectionErrors";
import { RootState, useAppDispatch } from "../../store/store";
import { ErrorLineText, ErrorRisedFullScreen } from "../common/error-rise/full-screen-error";
import s from './description.module.css';

const Description = () => {
    const { t } = useTranslation();  
    const dispatch = useAppDispatch();
    const connError: string = useSelector((state: RootState) => state.connectionError.errorText);

    const [errorOption, setErrorOption] = useState<number>(0);

    const btn1ClickHandler = () => {
        setErrorOption(1);
        // dispatch(changeErrorText("some error text!!!!"))
    }
    const btn2ClickHandler = () => {
        setErrorOption(2);
        // dispatch(changeErrorText("some error text!!!!"))
    }

    return <div className={s.description}>

        {/* { t("main_page_text_1")} */}
        
        { errorOption === 1 && <ErrorRisedFullScreen show={true} setShow={(bool) => setErrorOption(0) }/> }
        { errorOption === 2 && <ErrorLineText show={true} setShow={(bool) => setErrorOption(0) }/> }

        <ul>
            <li>Ощибка при логинизации: обработать все ошибки</li>
            <li>PUT /api/questionaries</li>
        </ul>

        <button onClick={btn1ClickHandler}>Ошибка вид № 1</button>
        <button onClick={btn2ClickHandler}>Ошибка вид № 2</button>

    </div>
}

export default Description