import { useState } from "react";
import { useSelector } from "react-redux";
import { RootState } from "../../../store/store";
import s from "./error.module.css";


type ErrorPropsType = {
    show: boolean,
    setShow: (bool: boolean) => void
}
export const ErrorRisedFullScreen = (props: ErrorPropsType) => {
    const connErrorText: string = useSelector((state: RootState) => state.connectionError.errorText);

    return <>
        {
            props.show && <div className={s.error_layout}>
                        <div className={s.alert_window}>
                            {connErrorText}
                            <button 
                                onClick={ () => props.setShow(false) }
                                className={s.alert_button}
                            >
                                OK
                            </button>
                        </div>
                    </div>
        }
   </>
}

export const ErrorLineText = (props: ErrorPropsType) => {
    const connErrorText: string = useSelector((state: RootState) => state.connectionError.errorText);
    setTimeout(() => {
        props.setShow(false);
    }, 3000)
    return <>
        {
            props.show && <div className={s.alert_line_div}>
                {connErrorText}
            </div>
        }
    </>
}
